
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Panel from 'vux/src/components/panel/index.vue'

export default {
components: {
ViewBox,
GroupTitle,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XButton,
Panel,
XHeader,
Flexbox,
FlexboxItem,
},
data() {
return {
disabled: false,
messageList: [],
title: '我的消息'
}
},
methods: {
onButtonClick(type) {
alert('on button click ' + type)
},
handleEvents(type) {
console.log('event: ', type)
},
update() {
this.$get("collection/getList", {},
res => {
console.log(res);
if(res.res >= 1) {
this.messageList = res.msg;
}
if(res.res > 1) {

}
});
},

del(item, index, list) {
this.$post("collection/change", {
goods_id: item.goods_id
},
res => {
console.log(res);
if(res.res >= 1) {
list.splice(index, 1);
this.$vux.toast.show({
text: "删除成功",
type: "text",
position: "middle"
});
}
if(res.res < 1) {
this.$vux.toast.show({
text: "删除失败",
type: "text",
position: "middle"
});
}

})
}
},
mounted() {
this.update();
if(this.$route.params["title"] != '' || this.$route.params["title"] != null) {
this.title = this.$route.params["title"];
}
if(this.$route.params["title"] == '' || this.$route.params["title"] == null) {

}
}
}



